<template>
  <div class="login-container">
    <!-- <vue-canvas-nest :config="config"></vue-canvas-nest> -->

    <div class="login-container-box">

      <div  class="info">
        <div  class="title">
          <img  src="data:image/png;base64,">
          <span >Blog后台管理</span></div>
        <div  class="detail">
          <span >
          <!--Database Manager 助您更好的管理关系型数据-->
        </span></div></div>

      <div class="login-form">


          <div slot="title" class="title">
            <h3>账号密码登录</h3>
          </div>
          <Form ref="formInline" :model="formInline" :rules="ruleInline">
            <FormItem prop="username">
              <Input type="text" v-model="formInline.username" placeholder="请输入用户名">
              <span slot="prepend">
                <Icon :size="16" type="md-person"></Icon>
              </span>
              </Input>
            </FormItem>
            <FormItem prop="password">
              <Input type="password" v-model="formInline.password" placeholder="请输入密码" @keyup.enter.native="handleSubmit('formInline')">
              <span slot="prepend">
                <Icon :size="14" type="md-lock"></Icon>
              </span>
              </Input>
            </FormItem>

           <!-- <FormItem prop="code">
              <Input type="text" v-model="formInline.code" placeholder="请输入验证码" @keyup.enter.native="handleSubmit('formInline')">
              <span slot="prepend">
                <Icon :size="14" type="ios-lock"></Icon>
              </span>
              <span class="show-pwd" slot="append" @click="getIdentifyCode"> {{gettingIdentifyCodeBtnContent}}</span>
              </Input>
            </FormItem> -->
           <FormItem prop="code">
              <Input type="text" v-model="formInline.code" placeholder="请输入右侧验证码" @keyup.enter.native="handleSubmit('formInline')">
              <span slot="prepend">
                <Icon :size="14" type="ios-lock"></Icon>
              </span>
              <img class="show-captcha" slot="append" :src="imagesrc" @click="changeSrc">
              </Input>
            </FormItem> 
            <FormItem>
              <Button type="primary" @click="handleSubmit('formInline')" long :loading="loading">登 录</Button>
            </FormItem>
          </Form>


      </div>
    </div>

 
  </div>
</template>

<script>
import vueCanvasNest from '@/components/VueCanvasNest'
import { mapGetters } from 'vuex'
import { setToken, setKey } from '@/utils/auth'
import * as UUID from 'uuidjs'
export default {
  name: 'Login',
  components: { vueCanvasNest },
  data () {
    return {
      qrcodeModal: false,
      config: { color: '255,255,255', opacity: '1', count: 240 },
      loading: false,
      bind_loading: false,
      oneCode: '',
      imagesrc: '',
      svgStr: '',
      uuid: '',
      formInline: {
        username: '',
        password: '',
        code: ''
      },
      ruleInline: {
        username: [
          {
            required: true,
            message: '请输入用户名！',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码！',
            trigger: 'blur'
          },
          {
            type: 'string',
            min: 5,
            message: '密码长度不能小于5位！',
            trigger: 'blur'
          }
        ],
        code: [
          {
            required: true,
            message: '请输入验证码！',
            trigger: 'blur'
          }
        ]
      },
      canGetIdentifyCode: true,
      gettingIdentifyCodeBtnContent: '获取验证码'
    }
  },
  computed: {
    ...mapGetters([
      'firstPage'
    ])
  },
  mounted () {
    this.uuid = UUID.generate()
    this.imagesrc = '/api/captcha/' + this.uuid
  },
  methods: {
    changeSrc () {
      this.uuid = UUID.generate()
      this.imagesrc = '/api/captcha/' + this.uuid
    },
    getIdentifyCode () {
      //   this.$refs.formInline.validate(valid => {
      // if (valid) {
      if (this.canGetIdentifyCode) {
        this.canGetIdentifyCode = false
        let timeLast = 20
        const timer = setInterval(() => {
          if (timeLast >= 0) {
            this.gettingIdentifyCodeBtnContent = timeLast + '秒后重试'
            timeLast -= 1
          } else {
            clearInterval(timer)
            this.gettingIdentifyCodeBtnContent = '获取验证码'
            this.canGetIdentifyCode = true
          }
        }, 1000)

        // you can write ajax request here
      }
      // }
      //   })
    },
    handleSubmit (name) {
      this.$refs[name].validate(valid => {
        this.loading = true
        if (valid) {
        
              this.api.login({ name: this.formInline.username, password: this.formInline.password, code: this.formInline.code, uuid: this.uuid })
              .then(res=>{
                setToken(res.access_token)
                setKey('expires_in',res.expires_in)
                setKey('timestamp',+new Date)
                this.api.getUserInfo().then(res=>{
                  this.$store.commit('SET_NAME',res.name)
                  setKey('user_name',res.name)
                  this.$store.dispatch('getUserMenu').then(()=>{
                  this.$router.push({ name: 'home' })
                  })
                })
              }).catch(err=>{
               if(err && err.response.data.message){
                this.$Message.error({
                  content: err.response.data.message,
                  duration: 3
                })
               }
                
              })
              
              
            }

        this.loading = false
      })
    },
    
  }
}
</script>

<style lang="less" scoped>
.login-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background-image: url('bg.svg');
  .login-container-box {
    position: absolute;
    width: 400px;
    height: 540px;
    margin: auto;
    top: 120px;
    right: 0;
    left: 0;

    .info {
      text-align: center;
      height: 120px;
      .title {
        display: flex;
        justify-content: center;
        align-items: Center;
        font-size: 32px;
        color: rgba(0,0,0,.85);
        font-family: Myriad Pro,Helvetica Neue,Arial,Helvetica,sans-serif;
        font-weight: 600;
      }
      .detail {
        font-size: 14px;
        color: rgba(0,0,0,.45);
        margin-top: 12px;
        margin-bottom: 40px;
      }
    }
    .login-form {
      border-radius: 5px;
      -moz-border-radius: 5px;
      background-clip: padding-box;
      padding: 35px 35px 15px;
      background: #fff;
      border: 1px solid #eaeaea;
      box-shadow: 0 0 25px #cac6c6;
      .title{
        margin: 0 auto 40px;
        text-align: center;
        color: #505458;
        font-size: 1.18em;
      }
    }
  }
}
.show-pwd {
  cursor: pointer;
}
.show-captcha {
  cursor: pointer;
  margin-top: -5px;
  margin-bottom: -8px;
  margin-left: -7px;
  margin-right: -7px;
}
</style>
